button {
  background: transparent;
  position: relative;
  padding: 14px 20px;
  display: flex;
  margin: auto;
  align-items: center;
  font-size: 17px;
  font-weight: 600;
  text-decoration: none;
  cursor: pointer;
  border: 1px solid rgb(98, 160, 217);
  border-radius: 25px;
  outline: none;
  margin-top: -70px;
  overflow: hidden;
  color: rgb(100, 167, 229);
  transition: color 0.3s 0.1s ease-out;
  text-align: center;
  z-index: 1;
}

button::before {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  content: '';
  border-radius: 50%;
  display: block;
  width: 20em;
  height: 20em;
  left: -5em;
  text-align: center;
  transition: box-shadow 0.5s ease-out;
  z-index: -1;
}

button:hover {
  color: #fff;
  border: 1px solid rgb(100, 163, 221);
}

button:hover::before {
  box-shadow: inset 0 0 0 10em rgb(105, 171, 232);
}


.on-color {
  color: red;
}

@media (max-width: 800px) {
  .container {
    margin-top: 10px;
  }

  h2 {
    font-size: 24px;
  }

  p {
    text-align: center;
    font-size: 9px;
    width: 20rem;
    margin-top: 15px;
  }

  .main-content {
    font-size: 16px;
    word-spacing: normal;
    width: 320px;
    justify-content: center;
    align-items: center;
    margin-left: 10px;
  }

  .btn-style {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 10px;
  }

  button {
    padding: 10px 20px;
    font-size: 16px;
    margin-top: 10px;

  }

  .counter-container {
    flex-direction: column;
  }

  .counter-container p:first-child {
    margin-bottom: 0;
  }
}